/* > Baomoi */

@font-face {
    font-family: 'ProximaCnBold';
    src: url('../fonts/proxima/ProximaNovaCond-Bold.woff2') format('woff2'),
    url('../fonts/proxima/ProximaNovaCond-Bold.woff') format('woff'),
    url('../fonts/proxima/ProximaNovaCond-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'proxima';
    src: url('../fonts/proxima/ProximaNova-Bold.woff2') format('woff2'),
    url('../fonts/proxima/ProximaNova-Bold.woff') format('woff'),
    url('../fonts/proxima/ProximaNova-Bold.ttf') format('truetype');
    font-weight: bold;
